<template>
    <div class="home">
          <transition name="fade">
      <div v-if="GameOn">
        <div class="container align-items-center" id="principal">
        <loading :active.sync="isLoading" :can-cancel="true" :is-full-page="fullPage" :opacity="1.0"></loading>
          <div class="d-flex flex-column justify-content-center align-items-center">
            <!-- <div class="col-sm-5" id="left">
                <leftComponent class="align-self-start"
                :numCorrect="numCorrect" :isfalse="isfalse"
                />
            </div> -->        
            <div class="score d-inline-flex">
                <h3 class="score"> Score: <b>{{numCorrect}}</b></h3>
                <div class="mt-1 hertz">
                    <span v-for="one in isfalse" :key="one" class="mx-2 mt-1" id="heart"><i class="fas fa-heart"></i></span>
                </div>
            </div>

            <div class="col-md-7">
                <QuestionBox v-if="questions.length" :next="next" 
                :Currentquestion="{question:questions[index], index}" 
                :increment="increment" :Isincorrect="Isincorrect" :isfalse="isfalse" :total="total"
                />
            </div>
          </div>
    </div> 
      </div>
      <div class="container text-center justify-content-center align-items-center" id="replay" style="height: 100vh;" v-else>
        <p id="gamOver">FIN DE JEU</p>
        <button class="btn mb-3 gradient plr60 ptb19" @click="Onrestart">REJOUER</button>
        <Input :numCorrect="numCorrect"/>
      </div>
     </transition>
    <div class="container text-center" v-if="index === this.questions.length">
          <div class="d-flex flex-column bd-highlight mb-3">
            <div class="p-2 bd-highlight"><button @click="onEnd" class="btn gradient color-white plr60 ptb19">RECOMMENCER</button>
          </div>
          </div><img v-if="GameOn" class="img-fluid p-2 bd-highlight" src="../assets/fin.jpg" /></div>
    </div>
</template>

<script>
// Import component
import Loading from "vue-loading-overlay";
import "vue-loading-overlay/dist/vue-loading.css";
import QuestionBox from "../components/QuestionBox";
import Input from '../components/input'
// import leftComponent from './components/leftComponent';
import axios from 'axios';

export default {
    name: 'home',
    data() {
    return {
      animate: false,
      isLoading: true,
      fullPage: true,
      questions: [],
      index: 0,
      numCorrect: 0,
      isfalse: 3,
      GameOn: true,
      total: null
    }
  },
  components: {
    QuestionBox,
    // leftComponent,
    Loading,
    Input
  },
  methods: {
    next(){
      this.index += 1;
    },
    increment(isCorrect){
      if(isCorrect){
        this.numCorrect += 10
      }
    },
    Isincorrect(){
      if(this.isfalse > 0){
       
        this.isfalse--;

      } 
      if(this.isfalse === 0 || this.index === this.questions.length){

          setTimeout(() => (this.GameOn = false), 500);
      }
    },
    Onrestart(){
      this.numCorrect = 0;
      this.GameOn = true;
      this.isfalse = 3
      this.index -= 1
    },
    onEnd(){
       this.numCorrect = 0;
      this.GameOn = true;
      this.isfalse = 3
      this.index = 0
    }
  },
  async created() {
    const res = await axios.get('api/questions');
    const data = await res.data;
    this.questions = await data;
    this.total = this.questions.length
    // hide page loader after 3 seconds
    setTimeout(() => {
      this.isLoading = false;
    },500);
  },
}
</script>

