<template>
    <div class="container">
        <h1>Ton score est de : <b>{{numCorrect}}</b></h1>
        <div class="" v-if="numCorrect >= 70">
            <div class="d-flex flex-column ">
            <h4 v-if="!posted"><em>Entres ton prénom pour enregistrer ton score au tableau des scores.</em></h4>
            <h4 v-else><em>Bravo, ton score à bien été ajouté au tableau des scores, découvres ton classement <router-link to="/" id="yo">ici</router-link></em></h4>
            </div>
            <input id="name" v-model="nom" :placeholder="placeholder" @keyup.enter="onSub" />
            <button class="btn gradient" @click="onSub">Enregistrer</button>
        </div>
        <div class="" v-else>
            <h4><em>Il te faut au moins 70 points pour pouvoir intégrer le classement des meilleurs scores</em></h4>
        </div>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    name: "Input",
    data() {
        return {
            nom: '',
            posted: false,
            placeholder: "Entrez votre nom ici..."
        }
    },
    props:['numCorrect'],
    methods: {
        async onSub(){
            if(this.nom.length > 2){
        
                let Player = {
                    Score: await this.numCorrect,
                    nom: await this.nom
                }
                await axios.post('api/score', Player)
                this.nom = await ''
                this.posted = true
            
            } else {
                this.placeholder = "Vous devez entrer votre nom avant de soumettre votre score"
            }
        }
    }
}
</script>

<style lang="scss" scoped>
#name {
    width: 70%;
    border: 4px solid #6065fb;
    border-radius: 20px;
    outline: none;
}
#yo{
    color: white !important;
}

</style>