<template>
  <div class="container" id="input">  
              <span>
                <input placeholder="Your question" v-model="question" >
              </span>
              <span>
                <input placeholder="correct answer" v-model="correct">
              </span>
              <span>
                <input placeholder="incorrect 1" v-model="incorrect1">
              </span>
              <span>
                <input placeholder="incorrect 2" v-model="incorrect2">
              </span>
              <span>
                <input placeholder="incorrect 3" v-model="incorrect3">
              </span>
              <span>
                <button @click.prevent="onPost">Submit</button>
              </span>
          </div>
</template>

<script>
    import axios from 'axios';

    export default {
        name: 'form',
        data() {
            return {           
                    question: '',
                    correct: '',
                    incorrect: [],
                    incorrect1 : '',
                    incorrect2 : '',
                    incorrect3 : ''   
            }
        },
        methods: {
            async onPost(){
                await this.incorrect.push(this.incorrect1, this.incorrect2, this.incorrect3);
          
                let questio = {
                    question: await this.question,
                    correct: await this.correct,
                    incorrect: await this.incorrect
                }
                await axios.post('/api/questions', questio);
                this.question = await ''
                this.incorrect = await []
                this.correct = await ''  
                this.incorrect1 = await ''                
                this.incorrect2 = await ''                
                this.incorrect3 = await ''                
            }
        },
    }
</script>

